*{
  margin:0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box
}

html,body{
  height: 100% !important;
}

.form-control{
  color:#fff !important;
}

.header{
  width: 100%;
}

.navbar,.navbar-default{
  background-color: #fff !important;
  border:0 !important;
  padding: 10px;
  height: 105px;
  border-radius: 0 !important;
  -webkit-box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2),2px 3px 8px rgba(0, 0, 0, 0.3);
          box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2),2px 3px 8px rgba(0, 0, 0, 0.3);
}

.contact-router{
  width: 60%;
}


.logo{
  display: inline-block;
  width: 90px;
  height:90px;
}
.brand{
  margin-top:20px;
  width: 300px;
  height: 45px;
}

.contact-nav{
  color:#354b99 !important;
  margin-top:27px;
}

/* welcome */
.App{
  width: 100%;
  background-color:#354b99;
  color:#fff;
  text-align: center;
  height:100vh;
  text-shadow: 0.2px 0.2px 0.2px;
  background-image: -webkit-gradient(linear,left top, left bottom,from(rgba(0,0,0,.5)),to(rgba(0,0,0,0.7))), url('./assets/background.jpg');
  background-image: -webkit-linear-gradient(rgba(0,0,0,.5),rgba(0,0,0,0.7)), url('./assets/background.jpg');
  background-image: -o-linear-gradient(rgba(0,0,0,.5),rgba(0,0,0,0.7)), url('./assets/background.jpg');
  background-image: linear-gradient(rgba(0,0,0,.5),rgba(0,0,0,0.7)), url('./assets/background.jpg');
  background-size: cover;
  font-family: 'lato';
  overflow: hidden;
}

.hero{
  padding:45px;
}

.tagline{
  margin:15vh auto 0;
  font-weight: bolder;
  font-size: 4em;
  text-shadow: 3px 3px 3px rgba(0, 0, 0, 0.9);
}

.about{
  margin:0 auto;
  width:60%;
  font-size: 18px;
  font-weight: lighter;
  margin:15px auto;
  margin-bottom: 15px !important;
}

.get-started,.get-started:hover,.get-started:focus{
  text-decoration: none;
  color:#fff;
  border:2px solid #fff;
  border-radius:25px;
  -webkit-box-shadow:5px 5px 5px 0  rgba(0, 0, 0, 0.3);
          box-shadow:5px 5px 5px 0  rgba(0, 0, 0, 0.3);
  padding:5px 15px;
  font-size: 1.25em;
  font-weight: bold;
  text-transform: uppercase;
}

/* second page */
.b{
  display: block;
  margin:15vh auto;
  width: 100%;
  color:#fff;
  border:0;
  position: relative;
  text-shadow: none;
  background-color:transparent;
  font-family: 'lato' !important;
  font-weight: bold;
  font-size:18px;
  outline:none;
  padding-bottom: 3px;
}

.b:hover,.b:focus{
  text-decoration: none !important;
  color:#fff;
}

.thumbnail{
  display: block;
  margin:1vh auto 0;
  color:#fff;
  background-color: rgba(0, 0, 0, 0.3);
  padding:2px 5px 2px 5px!important;
  border:3px solid #fff;
  overflow-y: scroll;
  height:75vh;
  -ms-overflow-style: none;
  overflow: -moz-scrollbars-none;
  scroll-behavior: smooth;
}
.thumbnail::-webkit-scrollbar {
  display: none;
}

.about-h{
  display: block;
  font-family: lato;
  margin:-15px auto 10px;
  font-weight: bold;
  text-transform: uppercase;
  text-align: center;
}
.about-p{
  display: block;
  margin:5px auto;
  text-align: center ;
  width: 90% ;
  font-size: 18px;
  font-family: lato;
  font-weight: lighter
}

.about-img{
  width: 500px;
  height: 300px;
  -webkit-box-shadow: 5px 5px 5px 0 rgba(0, 0, 0, 0.3);
          box-shadow: 5px 5px 5px 0 rgba(0, 0, 0, 0.3);
  margin-bottom: 2px;
}

.solutions-h{
  font-weight: bold;
  font-size: 1.15em;
  text-align: center;
  margin-left:5px;
  font-family: lato;
  text-transform: uppercase;
  text-shadow: 0 0 0 ;
}

.home-btn::before{
  content:"Home";
}

.about-btn::before{
  content:"About";
}

.solutions-btn::before{
  content:"Solutions";
}

.services-btn::before{
  content:"Services";
}

.products-btn::before{
  content:"Products";
}

.contact-btn::before{
  content:"Contact";
}

/* contact form */
.form-group{
  margin:10px auto;
  width:80%;
}

.form-control{
  background-color: transparent;
  font-weight: bold;
  border:2px solid #fff;
}

.send{
  background-color:#fff;
  color:rgba(0, 0, 0, 0.9);
  border-radius: 5px;
  font-weight: bold;
  border:0;
  font-size: 18px;
  padding:5px 45px;
  margin-bottom: 15px;
}

.below-form>.solutions-h,.below-form>.about-p{
  margin:10px auto 0 !important;
  text-align: center !important;
}

a,a:hover,a:focus{
  text-decoration: none;
  color:#fff;
}

